import {v1} from "uuid";

export type DialogsPageType = {
    dialogsData: Array<DialogsDataType>,
    messagesData: Array<MessagesData>,
    newMessage: string,
}
export type DialogsDataType = {
    id: number,
    name: string,
}
export type MessagesData = {
    id: string,
    message: string,
}
type ActionTypeDialogsPage =
    ReturnType<typeof AddMessageAC>
const Add_Message = 'Add_Message';
let initialState = {
    dialogsData: [
        {id: 1, name: 'Sasha'},
        {id: 2, name: 'Victor'},
        {id: 3, name: 'Dima'},
        {id: 4, name: 'Igor'},
        {id: 5, name: 'Anton'},

    ],
    messagesData: [
        {id: v1(), message: ' ,   -  ,      '},
        {id: v1(), message: '  7   56  ?'},
        {id: v1(), message: '  ?'},
        {id: v1(), message: ' ,   '},
        {id: v1(), message: ',   !!!'},
    ],
    newMessage: '',
}

export const dialogsPageReducer = (state: DialogsPageType = initialState, action: ActionTypeDialogsPage) => {
        switch (action.type) {
            case Add_Message: {
                let newMessage = {
                    id: v1(), message: action.message
                }
                let stateCopy = {...state}
                stateCopy.messagesData = [...stateCopy.messagesData, newMessage]
                return stateCopy;
            }

        }
    return state;
}
export const AddMessageAC = (message: string) => {
    return {
        type: "Add_Message",
        message: message
    } as const
}
